<template>
    <div class="bank-auth">
        <div class="block">
            <!-- <cell title="持卡人" defaultTxt="未填写" v-model="form.name" type="input" placeholder="请填入真实姓名"></cell> -->
            <!-- <cell title="身份证号" defaultTxt="未填写" v-model="form.idCard" type="input" placeholder="请填入身份证号"></cell> -->
            <cell title="银行卡号" defaultTxt="未填写" v-model="form.cardNo" type="input" placeholder="请输入"></cell>
            <cell title="银行预留手机号" defaultTxt="未填写" v-model="form.phone" type="input" placeholder="请输入"></cell>
            <!-- <cell title="手机号验证码" defaultTxt="未填写" v-model="form.phoneCode" placeholder="请输入">
                <input class="ver-code" type="text" placeholder="输入验证码">
                <cube-button class="code-btn" @click="sendCode" :disabled="codeBtn" :primary="true">{{codeBtn ? `再发送(${codeTime})` : '发送验证码'}}</cube-button>
            </cell> -->
        </div>
        <div class="banklist">
            <router-link to="/bankList">下款必看！可下款的银行卡列表 ></router-link>
        </div>
        <div>
            <cube-button class="btn-main re-btn" :style="[appConfig.mainBackground]"  @click="submit" :primary="true">提交认证</cube-button>
        </div>
    </div>
</template>

<script>
import cell from '../components/cell'
import { fetch, fetchHeader } from '../api/axios.js'
import { checkPhone } from '../utils/validator.js'
import { brigeNativeByJavascript } from '../api/Bridge.js'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            codeTime: 60,
            codeBtn: false,
            form: {
                cardNo: '',
                phone: '',
                phoneCode: undefined
                // name: '',
                // idCard: '',
                // callBackUrl: ''
            }
        }
    },
    computed: {
        ...mapState(['certification', 'user', 'appConfig'])
    },
    methods: {
        async sendCode() {
            console.log(checkPhone(this.form.phone, true))
            if (!checkPhone(this.form.phone, true)) return
            if (this.form.cardNo.length < 13) {
                this.$store.commit('setMsg', '银行卡号填写错误')
                return
            }
            this.verCodeTime()
            this.$store.commit('loading', true)
            const data = await fetchHeader('/auth/card/beginBindCard', { ...this.form }, 'post')
            if (data.code === 0) {
                this.$store.commit('setMsg', '发送成功！')
            }
        },
        verCodeTime() {
            this.codeBtn = true
            const time = () => {
                setTimeout(() => {
                    if (this.codeTime === 0) {
                        this.codeBtn = false
                        this.codeTime = 60
                    } else {
                        this.codeTime -= 1
                        time()
                    }
                }, 1000)
            }
            time()
        },
        async submit() {
            if (!checkPhone(this.form.phone, true)) return
            if (this.form.cardNo.length < 13) {
                this.$store.commit('setMsg', '银行卡号填写错误')
                return
            }
            this.$store.commit('loading', true)
            setTimeout(() => {
                let certification = this.user.certification
                certification.bank = true
                this.$store.commit('setUser', {certification})
                this.$store.commit('setMsg', '认证成功')
                this.$store.commit('loading', false)
                this.$router.go(-1)
            }, 2000)
            // const data = await fetchHeader('/auth/card/userBankCardBind', { ...this.form }, 'post')
            // if (data.code && data.code === 0) {
            //     this.$store.commit('setMsg', '操作成功！')
            //     if (this.$route.query.form) {
            //         this.$router.go(-1)
            //     } else {
            //         brigeNativeByJavascript('closeApplication')
            //     }
            // }
        }
    },
    beforeCreate() {
        document.title = '银行卡认证'
    },
    components: {
        cell
    }
}
</script>

