<template>
    <div class="cell">
        <div class="title">
            {{title}}
        </div>
        <div class="content">
            <input v-if="type === 'input'" type="text" v-model="val" :placeholder="placeholder">
            <slot v-else></slot>
            <!-- <span @click="inputClick">{{value ? value : defaultTxt}} ></span> -->
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: '标题'
        },
        defaultTxt: {
            type: String,
            default: '未填写'
        },
        type: {
            type: String,
            default: ''
        },
        placeholder: {
            type: String,
            default: ''
        },
        value: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            val: this.value
        }
    },
    methods: {},
    watch: {
        value(n) {
            this.val = n
        },
        val(n) {
            this.$emit('input', n)
        }
    }
}
</script>

